.sick-leave__form {
  padding: 0 8px 8px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  min-width: 400px;
  @media (max-width: 490px) {
    min-width: 0;
  }
}
.form {
  &__section {
    display: flex;
    flex-direction: column;
    gap: 16px;
    span {
      font-weight: 600;
    }
  }
}
